//vuevuex
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store({  
    state: {  
        userInfo: {},  
        hasLogin: false  
    },  
    mutations: {  
        login(state, provider) {//  
            state.hasLogin = true  
			state.userInfo=provider
            //state.userInfo.token = provider.token
            state.userInfo.userName = provider.client.user_name
            state.userInfo.userId = provider.client.uid //id
			state.userInfo.companyName = provider.client.profiles.user_company.company_name
			state.userInfo.user_Id =provider.client.profiles.user_info.user_id //Id
			state.userInfo.user_type = provider.client.profiles.user_info.type //
			state.userInfo.isIdentifiy = provider.client.profiles.user_info.is_identifiy //
            uni.setStorage({//  
                key: 'userInfo',  
                data: provider  
            })  
        },  
        logout(state) {//  
            state.hasLogin = false  
            state.userInfo = {}  
            uni.removeStorage({  
                key: 'userInfo'  
            })  
        }  
    }  
})
export default store